import { AbstractControl, ValidatorFn, ValidationErrors, FormGroup } from '@angular/forms';

export function matchPass(pass: string, confirmPass: string): ValidatorFn {
  return (control: FormGroup): ValidationErrors | null => {
    if (control.controls[pass].value && control.controls[confirmPass].value) {
      if (control.controls[pass].value !== control.controls[confirmPass].value) {
        return {notMatchPass: true};
      }
    }
    return null;
  };
}
